.line-runners {
    top: 0;
    left: 0;
    right: 0;
    z-index: 999998;
    height: 100%;
    margin: auto;
    position: fixed;
    pointer-events: none;
}

.line-runners .line {
    top: 0;
    left: 50%;
    width: 1px;
    height: 100%;
    overflow: hidden;
    position: absolute;
    background: rgba(255, 255, 255, .1);
}

.line-runners .line:after {
    left: 0;
    top: -50%;
    content: "";
    width: 100%;
    height: 15vh;
    display: block;
    position: absolute;
    -webkit-animation: run 7s 0s infinite;
            animation: run 7s 0s infinite;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: cubic-bezier(.4, .26, 0, .97);
            animation-timing-function: cubic-bezier(.4, .26, 0, .97);
    background: linear-gradient(
        to bottom, #f00, #ffa500, #ff0, #0F0, #00f, #4b0082, #ee82ee, #f00
    );
}

